import React, { useState, useEffect } from "react";
import axios from "axios";
import { useHistory, useParams } from "react-router-dom";
import { BASE_URL, FILES_URL } from "../../constants/index";
import Navbar from "../layout/Navbar";

const EditUser = () => {
  let history = useHistory();
  const { id } = useParams();
  const [name, setname] = useState('');
  const [price, setprice] = useState();
  const [pImage, setpImage] = useState();



  const onFileChange = (e) => {
    const pImage = e.target.files[0];
    setpImage(pImage);
  };
  useEffect(() => {
    loadUser();
  }, []);

  const onSubmit = async e => {
    e.preventDefault();
    const data = new FormData();
    data.append("productId", id);
    data.append("name", name);
    data.append("price", price);
    data.append("sell_point_id", localStorage.getItem("sell_point_id"));
    data.append("pImage", pImage);
    await axios.post(`${BASE_URL}/editProduct`, data, {
      headers: {
        "x-access-token": localStorage.getItem("token")
      }
    });
    history.push("/");
  };

  const loadUser = async () => {
    const data = {
      pId: id,
      sell_point_id: localStorage.getItem("sell_point_id")
    }
    const res = await axios.post(`${BASE_URL}/getProductInfo`, data, {
      headers: {
        "x-access-token": localStorage.getItem("token")
      }
    });
    setpImage(res.data.photo);
    setname(res.data.name);
    setprice(res.data.price);
  };
  return (
    <>
      <Navbar />
      <div className="container">
        <div className="w-75 mx-auto shadow p-5">
          <h2 className="text-center mb-4">تعديل المنتج</h2>
          <form onSubmit={e => onSubmit(e)}>
          <div className="form-group">
              <td ><img style={{ width: 250, height: 250 }} src={`${FILES_URL}${pImage}`} /></td>

            </div>
            <div className="form-group">
              <input
                type="file"
                className="form-control form-control-lg"
                placeholder="Enter Your Website Name"
                name="pImage"
                onChange={(e) => onFileChange(e)}
              />
            </div>
  

            <div className="form-group">
              <input
                type="text"
                className="form-control form-control-lg"
                placeholder="Enter Your Name"
                name="name"
                value={name || ''}
                onChange={e => setname(e.target.value)}
                style={{ margin: 15 }}
              />
            </div>
                      <div className="form-group">
              <input
                type="text"
                className="form-control form-control-lg"
                placeholder="Enter Your price Number"
                name="price"
                value={price || ''}
                onChange={(e) => setprice(e.target.value)}
                style={{ margin: 15 }}
              />
            </div>

            <button className="btn btn-warning btn-block" style={{ margin: 15 }} >حفظ</button>
          </form>
        </div>
      </div>

    </>

  );
};

export default EditUser;
