import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { BASE_URL } from '../../constants/index';
import Swal from "sweetalert2";
import { Form, Input, Cascader, Select, Row, Col, Checkbox, Button, PageHeader, Card } from 'antd';
import { useHistory, Link } from "react-router-dom"
import { Container } from '../layout/Container';

import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";
const { Option } = Select;

const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

const Register = () => {
  const [CpTypes, setCpTypes] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [address, setAddress] = useState("");
  const [lat, setLat] = useState();
  const [lng, setLng] = useState();
  let history = useHistory()

  const handleSelect = async (value) => {
    const results = await geocodeByAddress(value);
    const latLng = await getLatLng(results[0]);
    setAddress(value);
    setLat(latLng.lat)
    setLng(latLng.lng)
  };


  useEffect(() => {
    const getCpTypes = async () => {
      try {
        setIsLoading(true);
        const result = await axios.get(`${BASE_URL}/getCpTypes`);
        setCpTypes(result.data);
        // console.log(result.data);
        setIsLoading(false);

      } catch (error) {
        setIsLoading(false);

        if (error.response) {
          console.log('error', error.response.data);
        }
      }

    };

    getCpTypes();
  }, []);

  const [form] = Form.useForm();

  const onFinish = async (values) => {
    console.log('Received values of form: ', values);
    console.log("lat", lat);
    console.log("lng", lng);

    var location = {
      "lat": lat,
      "lng": lng
    }



    // console.log(body);
    try {
      const config = { headers: { "Content-Type": "multipart/form-data" } };

      await axios.post(`${BASE_URL}/createCp`, {
        ...values,
        ...location
      });
      Swal.fire("رائع!", "لقد قمت بالتسجيل بنجاح سوف يتم التواصل معك و تفعيل حسابك!", "success").then(
        (result) => {
          if (result.isConfirmed || result.isDismissed) {
            console.log("done");
            history.push("/login")

          }
        }
      );
    } catch (error) {
      if (error.response) {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: error.response.data.msg,
        });
        console.log("error", error.response.data);
      }
    }



  };



  return (
    <Container>
      <PageHeader
        style={{ border: '1px', textAlign: 'right', fontFamily: 'fantasy' }}
        onBack={() => null}
        title="جيتك"
        subTitle="نقطة بيع "
      />
      <Row justify='center' align='middle'  >

        <Col lg={24} md={18} xs={24} sm={24} >
          <Card >
            <div className=" mx-auto shadow p-5">
              <Form
                {...formItemLayout}
                form={form}
                name="register"
                onFinish={onFinish}
                scrollToFirstError
                style={{ fontFamily: "monospace", fontWeight: 'bold' }}
                initialValues={{
                  remember: true
                }}
              >
                <Form.Item
                  name="cpName"
                  label=" إٍسم نقطة البيع "
                  rules={[
                    {
                      message: 'الرجاء إدخال الاسم الصحيح ',
                    },
                    {
                      required: true,
                      message: 'الرجاء ادخال اسم نقطة البيع',
                    },
                  ]}
                  style={{color:'red'}}
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  name="email"
                  label="البريد الالكتروني"
                  rules={[
                    {
                      type: 'email',
                      message: 'الرجاء إدخال بريد الكتروني صحيح ',
                    },
                    {
                      required: true,
                      message: 'الرجاء ادخال البريد الالكتروني !',
                    },
                  ]}
                >
                  <Input />
                </Form.Item>

                <Form.Item
                  name="password"
                  label="كلمة المرور "
                  rules={[
                    {
                      required: true,
                      message: 'الرجاء إدخال كلمة المرور !',
                    },
                  ]}
                  hasFeedback
                >
                  <Input.Password />
                </Form.Item>

                <Form.Item
                  name="confirm"
                  label="تاكيد كلمة المرور "
                  dependencies={['password']}
                  hasFeedback
                  rules={[
                    {
                      required: true,
                      message: 'الرجاء تأكيد كلمة المرور!',
                    },
                    ({ getFieldValue }) => ({
                      validator(_, value) {
                        if (!value || getFieldValue('password') === value) {
                          return Promise.resolve();
                        }

                        return Promise.reject(new Error('كلمات المرور غير متطابقة '));
                      },
                    }),
                  ]}
                >
                  <Input.Password />
                </Form.Item>


                <Form.Item
                  name="mobile"
                  label="رقم هاتف المحل "

                  rules={[
                    {
                      required: true,
                      message: 'الرجاء ادخال رقم هاتف المحل ',
                      type:'number'
                    },
                  ]}
                >
                  <Input
                    placeholder="05XXXXXXXXX"

                  />
                </Form.Item>

                <Form.Item
                  name="commercialRegister"
                  label="  رقم السجل التجاري للمحل  "

                  rules={[
                    {
                      required: true,
                      message: 'الرجاء ادخال   السجل التجاري  ',
                    },
                  ]}
                >
                  <Input
                    placeholder="1XXXXXXXXXXXXX"

                  />
                </Form.Item>

                <Form.Item
                  name="cpType"
                  label="نوع نقطة البيع "
                  rules={[
                    {
                      required: true,
                      message: 'الرجاء اختيار نوع المحل !',
                    },
                  ]}
                >
                  <Select placeholder="اختر نوع المحل ">
                    {CpTypes.length > 0 ? (
                      CpTypes.map(({ _id, name }) => (
                        <option value={_id} key={_id}>
                          {name}
                        </option>
                      ))
                    ) : (
                      <option value="" key="">
                        لا توجد بيانات
                      </option>
                    )}
                  </Select>
                </Form.Item>



                <Form.Item
                  name="location"
                  label=" الموقع علي خرائط جوجل   "

                  rules={[
                    {
                      required: true,
                      message: ' الرجاء البحث و اختيار موقع   ',
                    },
                  ]}
                >
                  <PlacesAutocomplete
                    required
                    value={address}
                    onChange={setAddress}
                    onSelect={handleSelect}
                  >
                    {({
                      getInputProps,
                      suggestions,
                      getSuggestionItemProps,
                      loading,
                    }) => (
                      <div>
                        <p>العنوان : {address}</p>

                        <input style={{
                          width: '100%',
                        }} {...getInputProps({ placeholder: "إبحث عن الموقع" })} />

                        <div>
                          {loading ? <div>...loading</div> : null}

                          {suggestions.map((suggestion) => {
                            const style = {
                              backgroundColor: suggestion.active ? "#41b6e6" : "#fff",
                            };
                            return (
                              <div {...getSuggestionItemProps(suggestion, { style })}>
                                {suggestion.description}
                              </div>
                            );
                          })}
                        </div>
                      </div>
                    )}
                  </PlacesAutocomplete>
                </Form.Item>

                <Form.Item
                  name="agreement"
                  valuePropName="checked"
                  rules={[
                    {
                      validator: (_, value) =>
                        value ? Promise.resolve() : Promise.reject(new Error('Should accept agreement')),
                    },
                  ]}
                  {...tailFormItemLayout}
                >
                  <Checkbox>
                    لقد قمت بالاطلاع على الشروط  <a href="" target="_blank">الاتفاقية</a>
                  </Checkbox>
                </Form.Item>

                <Form.Item {...tailFormItemLayout}>
                  <Button type="primary" htmlType="submit" style={{ fontFamily: 'monospace' }}>
                    تسجيل
                  </Button>
                  <Link to="/login" type="primary" style={{ fontFamily: 'monospace', marginRight: 20 }}>
                    تسجيل دخول
                  </Link>
                </Form.Item>
              </Form>
            </div>
          </Card>
        </Col>
      </Row>

    </Container>
  );
};

export default Register;
