import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link ,useHistory } from "react-router-dom";
import { BASE_URL } from '../../constants/index';
import { FILES_URL } from '../../constants/index';
import Navbar from "../layout/Navbar";
import Modal from "@material-ui/core/Modal";
import Notification from "../AlertMessageBox";
import ConfirmDialog from "../ConfirmDialog";
import { makeStyles } from "@material-ui/core/styles";
import ImgMediaCard from "../Card";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import ListSubheader from "@material-ui/core/ListSubheader";

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,

  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "space-around",
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: '100%',
    height: 450,
  },
  icon: {
    color: "rgba(255, 255, 255, 0.54)",
  },
  paper: {
    position: "absolute",
    width: 600,
    height: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  profileimage: {
    width: 150,
    height: 150,
    margin: 8,

  },
}));
const Home = () => {
  let history = useHistory()
  const classes = useStyles();

  const [users, setUser] = useState([]);
  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });
  const [confirmDialog, setConfirmDialog] = useState({
    isOpen: false,
    title: "",
    subTitle: "",
  });
  const [open, setOpen] = useState(false);

  useEffect(() => {
    if (localStorage.getItem('token')) {
      history.push('/');
    } else {
      history.push('/login');
    }
    loadUsers();

  }, []);
  const sell_point_id = localStorage.getItem("sell_point_id")
  const handleClose = () => {
    setOpen(false);
  };
  const editProduct=(id)=>{
    console.log(id);
    history.push(`/users/edit/${id}`)

  }
  const confirm = (id) => {
    setConfirmDialog({
      isOpen: true,
      title: "   هل انت متاكد من حذف المنتج ؟",
      subTitle: "لا يمكنك التراجع عن هذة العملية ",
      onConfirm: () => {
        deleteProduct(id);
      },
    });
  };

  const loadUsers = async () => {
    const res = await axios.post(`${BASE_URL}/getCpProducts`, { sell_point_id }, {
      headers: {
        "x-access-token": localStorage.getItem("token")
      }
    });
    setUser(res.data.reverse());
    // console.log(res.data);
  };

  const deleteProduct = async id => {
    console.log("deleted id " + id);
    const data = {
      pId: id,
      sell_point_id: sell_point_id
    }
    await axios.post(`${BASE_URL}/deleteProdutc`, data, {
      headers: {
        "x-access-token": localStorage.getItem("token")
      }
    });
    window.location.reload();

  };

  return (
    <>
      <Navbar />
      <div className="container">
      <GridList cellHeight={180} className={classes.gridList}>
          <GridListTile key="Subheader" style={{ height: "auto" }}>
            <ListSubheader component="div">الكباتن</ListSubheader>
          </GridListTile>

          <h1>المنتجات </h1>
      {users.map((user)=>(
         <ImgMediaCard
         data={user}
         statusChangeHandler={editProduct}
         statusRemoveHandler={confirm}
       />
      ))}
        </GridList>


          {/* {
            !users ? "لا توجد لديكم منتجات " : (
              <table className="table border shadow">
                <thead className="thead-dark">
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">إسم المنتج </th>
                    <th scope="col">صورة المنتج </th>
                    <th scope="col">السعر </th>
                    <th>إجراء</th>
                  </tr>
                </thead>
                <tbody>
                  {users.map((user, index) => (
                    <tr key={user._id}>
                      <th scope="row">{index + 1}</th>
                      <td>{user.name}</td>
                      <td ><img style={{ width: 250, height: 250 }} src={`${FILES_URL}${user.photo}`} /></td>
                      <td>{user.price}</td>
                      <td>
                        <Link className="btn btn-primary mr-2" style={{ margin: 15 }} to={`/users/${user._id}`}>
                          عرض تفاصيل المنتج
                        </Link>
                        <Link style={{ margin: 15 }}
                          className="btn btn-outline-primary mr-2"
                          to={`/users/edit/${user._id}`}
                        >
                          تعديل
                        </Link>
                        <button style={{ margin: 15 }}
                          className="btn btn-danger"
                          onClick={() => confirm(user._id)}
                        >
                          حذف
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>

            )
          } */}

      </div>
      <Notification notify={notify} setNotify={setNotify} />
      <ConfirmDialog
        confirmDialog={confirmDialog}
        setConfirmDialog={setConfirmDialog}
      />
    </>

  );
};

export default Home;
