import React, { useState, useEffect } from "react";
import { BASE_URL, FILES_URL } from "../../constants/index";
import axios from "axios";
import { Container, Row, Col, Form, Button } from "react-bootstrap";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";
import Navbar from "../layout/Navbar";

import { useHistory } from "react-router-dom";

const About = () => {
  let history = useHistory();

  const [poinOfSellData, setpoinOfSellData] = useState([]);

  const [pImage, setpImage] = useState();
  const [address, setAddress] = useState("");
  const [lat, setLat] = useState(poinOfSellData.lat);
  const [lng, setLng] = useState(poinOfSellData.lng);
  const [phone, setPhone] = useState(poinOfSellData.phone);
  const [email, setEmail] = useState(poinOfSellData.email);
  const [name, setName] = useState(poinOfSellData.cpName);

  // const profileImage = `${FILES_URL}${poinOfSellData.cpImage}`
  const handleSelect = async (value) => {
    const results = await geocodeByAddress(value);
    const latLng = await getLatLng(results[0]);
    setAddress(value);
    setLat(latLng.lat)
    setLng(latLng.lng)
  };

  const fetch = async () => {
    const body = {
      spId: localStorage.getItem("sell_point_id"),
    };
    axios
      .post(`${BASE_URL}/getSPIfo`, body, {
        headers: {
          "x-access-token": localStorage.getItem("token")

        },
      })
      .then((response) => {
        // console.log(response.data);
        setName(response.data.cpName);
        setPhone(response.data.phone);
        setEmail(response.data.email);
        setpImage(response.data.cpImage);
      })
      .catch((err) => console.log(err));
  };

  const UpdateProfileHandler = async (e) => {
    e.preventDefault();

    const body = {
      name: name,
      email: email,
      phone: phone,
      sell_point_id: localStorage.getItem("sell_point_id")
    }

    await axios.post(`${BASE_URL}/updatePointOfSellData`, body, {
      headers: {
        "Content-Type": "application/json",
        "x-access-token": localStorage.getItem("token")

      },
    });
    window.location.reload();
  };


  const updateImageHandler = async (e) => {
    e.preventDefault();
    const data = new FormData();

    data.append("sell_point_id", localStorage.getItem("sell_point_id"));
    data.append("pImage", pImage);
    await axios.post(`${BASE_URL}/updatePointOfSellImage`, data, {
      headers: {
        "Content-Type": "multipart/form-data",
        "x-access-token": localStorage.getItem("token")

      },
    });
    window.location.reload();
  }

  const onFileChange = (e) => {
    const pImage = e.target.files[0];
    setpImage(pImage);
  };


  useEffect(() => {
    if (localStorage.getItem('token')) {
      history.push('/about');
    } else {
      history.push('/login');
    }
    fetch();
  }, [1000]);

  return (
    <>
      <Navbar />
      <Container>
        <Row>
          <Col>
            <img
              style={{ width: 200, height: 200, borderRadius: 10, marginTop: 10 }}
              src={`${FILES_URL}${pImage}`}
              alt="profils pic"
            />
            <Form className="form" style={{margin:15}} onSubmit={updateImageHandler}>
              <Form.Group controlId="formCategory4">
                <Form.Label>  إختيار صورة لنقطة البيع  </Form.Label>
                <Form.Control
                  required
                  type="file"
                  name="profileImage"
                  onChange={(e) => onFileChange(e)}
                />
              </Form.Group>
              <Button variant="primary" style={{margin:15}} type="submit" >
                تغيير الصورة
              </Button>
            </Form>
          </Col>
          <Col>
            <h1>بيانات نقطة البيع </h1>
            <Form className="form" onSubmit={UpdateProfileHandler} >
              {/* <p>{this.state.msg}</p> */}
              <Form.Group controlId="formCategory1" style={{margin:15}}>
                <Form.Label>إسم المحل </Form.Label>
                <Form.Control type="text" onChange={(e) => setName(e.target.value)}
                  required value={name || ''} />
              </Form.Group>
              <Form.Group controlId="formCategory2" style={{margin:15}}>
                <Form.Label>البريد الالكتروني </Form.Label>
                <Form.Control type="email" onChange={(e) => setEmail(e.target.value)}
                  required value={email || ''} />
              </Form.Group>
              <Form.Group controlId="formCategory2" style={{margin:15}}>
                <Form.Label> رقم الهاتف </Form.Label>
                <Form.Control type="number" required onChange={(e) => setPhone(e.target.value)}
                  value={phone || ''} />
              </Form.Group>

              {/* <Form.Group controlId="formCategory5">
              <Form.Label> موقع نقطة البيع  </Form.Label>
              <PlacesAutocomplete
                required
                value={address}
                onChange={setAddress}
                onSelect={handleSelect}
              >
                {({
                  getInputProps,
                  suggestions,
                  getSuggestionItemProps,
                  loading,
                }) => (
                  <div>
                    <p>العنوان : {address}</p>

                    <input {...getInputProps({ placeholder: "إبحث عن الموقع" })} />

                    <div>
                      {loading ? <div>...loading</div> : null}

                      {suggestions.map((suggestion) => {
                        const style = {
                          backgroundColor: suggestion.active ? "#41b6e6" : "#fff",
                        };
                        return (
                          <div {...getSuggestionItemProps(suggestion, { style })}>
                            {suggestion.description}
                          </div>
                        );
                      })}
                    </div>
                  </div>
                )}
              </PlacesAutocomplete>
            </Form.Group> */}
              <Button variant="primary" style={{margin:15}} type="submit" >
                تحديث البيانات
              </Button>
            </Form>
          </Col>
        </Row>
      </Container>

    </>


  );
};

export default About;
