import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import { BASE_URL, FILES_URL } from "../../constants/index";

const User = () => {

  const [user, setUser] = useState({
    name: "",
    username: "",
    email: "",
    phone: "",
    webiste: ""
  });
  const { id } = useParams();
  console.log(id);
  useEffect(() => {
    loadUser();
  }, []);
  const loadUser = async () => {
    console.log(id);
    const data = {
      pId : id,
      sell_point_id : localStorage.getItem("sell_point_id")
    }
    const res = await axios.post(`${BASE_URL}/getProductInfo`,data,{
      headers:{
        "x-access-token":localStorage.getItem("token")
      }
    });
    setUser(res.data);
    console.log(res.data);
  };
  return (
    <div className="container py-4">
      <Link className="btn btn-primary" to="/">
        الرجوع للصفحة الرئيسية 
      </Link>
      <h1 className="display-4" style={{margin:15}}>إسم المنتج: {user.name}</h1>
      <hr />
      <ul className="list-group w-50" style={{margin:15}}>
        <li className="list-group-item" style={{margin:15}}>إسم المنتج: {user.name}</li>
      
        <li className="list-group-item" style={{margin:15}}>السعر: {user.price}</li>
        <td ><img style={{width:250 , height:250}} src={`${FILES_URL}${user.photo}`}/></td>
      </ul>
    </div>
  );
};

export default User;
