import React from 'react'
import { useHistory, Link, NavLink } from "react-router-dom"
import { Form, Input, Cascader, Select, Row, Col, Checkbox, Button, PageHeader, Card } from 'antd';
import axios from 'axios';
import { BASE_URL } from '../../constants/index';
import Swal from "sweetalert2";
import 'antd/dist/antd.css';
import { Container } from '../layout/Container';
const formItemLayout = {
    labelCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 8,
        },
    },
    wrapperCol: {
        xs: {
            span: 24,
        },
        sm: {
            span: 16,
        },
    },
};
const tailFormItemLayout = {
    wrapperCol: {
        xs: {
            span: 24,
            offset: 0,
        },
        sm: {
            span: 16,
            offset: 8,
        },
    },
};

const Login = () => {
    let history = useHistory()

    const [form] = Form.useForm();
    const onFinish = async (values) => {
        console.log('Received values of form: ', values);




        // console.log(body);
        try {
            const config = { headers: { "Content-Type": "multipart/form-data" } };

            await axios.post(`${BASE_URL}/pointOfSellLogin`, {
                ...values,
            }).then((response) => {
                console.log(response.data.result._id);
                localStorage.setItem("token", response.data.token)
                localStorage.setItem("sell_point_id", response.data.result._id)
            })

                ;
            Swal.fire("رائع!", "لقد قمت بتسجيل الدخول  بنجاح!", "success").then(
                (result) => {
                    history.push("/")

                    if (result.isConfirmed || result.isDismissed) {
                        console.log("done");
                    }
                }
            );
        } catch (error) {
            if (error.response) {

                Swal.fire({
                    icon: "error",
                    title: "Oops...",
                    text: error.response.data.msg,
                });
                console.log("error", error.response.data);
            }
        }



    };
    return (
        <Container>
            <PageHeader
                className="site-page-header"
                onBack={() => null}
                title="جيتك"
                subTitle="نقطة بيع "
                style={{fontFamily:'fantasy'}}
            />

            <Row justify='center'  >
                <Col lg={10} md={18} xs={24} sm={24} >
                    <Card >
                        <Form
                            {...formItemLayout}
                            form={form}
                            name="register"
                            onFinish={onFinish}

                            scrollToFirstError
                            style={{ fontFamily: "monospace", fontWeight: 'bold' }}
                        >
                            <Form.Item
                                name="email"
                                label="البريد الالكتروني"
                                rules={[
                                    {
                                        type: 'email',
                                        message: 'الرجاء إدخال بريد الكتروني صحيح ',
                                    },
                                    {
                                        required: true,
                                        message: 'الرجاء ادخال البريد الالكتروني !',
                                    },
                                ]}
                            >
                                <Input />
                            </Form.Item>

                            <Form.Item
                                name="password"
                                label="كلمة المرور "
                                rules={[
                                    {
                                        required: true,
                                        message: 'الرجاء إدخال كلمة المرور !',
                                    },
                                ]}
                                hasFeedback
                            >
                                <Input.Password />
                            </Form.Item>

                            <Form.Item {...tailFormItemLayout}>
                                <Button type="primary" htmlType="submit" style={{ fontFamily: 'monospace' }}>
                                    تسجيل دخول
                                </Button>
                                <Link to="/register" style={{ fontFamily: 'monospace', margin: 5 }}>
                                    حساب جديد
                                </Link>
                            </Form.Item>
                        </Form>
                    </Card>
                </Col>
            </Row>
        </Container>
    )
}

export default Login
